<template>
  <div class="submit-form">
    <div v-if="!submitted">
      <div class="form-group">
        <label for="message-content">Message</label>
        <textarea
          type="text"
          :class="{ [`form-control`]:true, [`has-error`]: contentError }"
          id="message-content"
          required
          v-model="message.content"
          name="message"
          v-on:change="handleFieldChange"
        />
      </div>

      <div class="form-group">
        <label for="encryption_type">Encryption Type</label>
        <select
          :class="{ [`form-control`]:true, [`has-error`]: encryptionTypeError }"
          id="encryption-type"
          required
          v-model="message.encryption_type"
          name="encryption-type"
          v-on:change="handleFieldChange"
        >
          <option value="backwards">Backwards</option>
          <option value="emo-gize">Emo-gize</option>
          <option value="letter-scramble">Letter-scramble</option>
          <option value="nothing">Nothing</option>
        </select>
      </div>

      <button @click="saveMessage" class="btn btn-success">Save</button>
    </div>

    <div v-else>
      <h4>You have submitted successfully!</h4>
      <button class="btn btn-success" @click="newMessage">NEW</button>
    </div>
  </div>
</template>

<script>
import MessageDataService from "../services/MessageDataService";

export default {
  name: "add-message",
  data() {
    return {
      message: {
        content: "",
        encryption_type: ""
      },
      contentError: false,
      encryptionTypeError: false,
      submitted: false
    };
  },
  methods: {
    handleFieldChange(e){
      switch (e.target.name) {
        case "message":
            if(e.target.value === undefined || (typeof e.target.value === "string" && e.target.value.trim() === "")){
              this.contentError = true;
            }else{
              this.contentError = false;
            }
            break;
        case "encryption-type":
            if(e.target.value === undefined){
              this.encryptionTypeError = true;
            }else{
              this.encryptionTypeError = false;
            }
            break;
        default:
      }
    },
    saveMessage() {
      var data = {
        content: this.message.content,
        encryption_type: this.message.encryption_type
      };
      if(data.content === undefined || (typeof data.content === "string" && data.content.trim() === "")){
        this.contentError = true;
      }
      if(data.encryption_type === undefined || !data.encryption_type){
        this.encryptionTypeError = true;
      }
      if(this.contentError || this.encryptionTypeError){
        return;
      }
      MessageDataService.create(data)
        .then(response => {
          if(response.data.success){
            alert("Created Successfully");
            this.submitted = true;
          }else{
            alert("Something went wrong");
          }
        })
        .catch(e => {
          console.log(e);
        });
    },
    
    newMessage() {
      this.submitted = false;
      this.message = {
        content: "",
        encryption_type: ""
      };
    }
  }
};
</script>

<style>
.submit-form {
  max-width: 300px;
  margin: auto;
}
</style>